
@import '../../../scss/variables';

.list{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    cursor: pointer;
}
.array{
    padding: 40px 0;
    display: flex;
    flex-direction: row;
    gap: 20px;

    div{
        text-align: center;
        background-color: $orange;
        padding: 20px;
        border-radius: 40px;
        transition: .3s;
        &:hover{
            background-color: darken($orange, 8);
            transform: scale(1.1);
        }
        p{
            color: white;
        }
    }
}

@media (max-width: 700px ) {
    .list{
        width: 100%
    }
    .array  {
        width: 100%;
        overflow: scroll;
    }
}