
.good-page__title{
  display: flex;
  justify-content: center;
  align-items:center;
  padding: 2rem 0 4rem;
  h2{
    font-size: 4rem;
    line-height: 5rem;    
  }
}
.good-page__content {
  display: flex;
}
.good-page__info{
  max-height: 290px;
  img{
    width: 350px;
    height: 400px;
  }
}
.good-page__about {
  padding: 2rem 4rem;
  p{
    padding-top: 2rem;
    letter-spacing: 1.3px;
    &:first-letter {
      text-transform: uppercase;
  }
  }
}
.good-page__statistic{
  display: flex;
  justify-content: space-between;
  padding: 2rem 0;
  span{
    font-weight: 500;
  }
}

.good-page__other span{
  color: $orange;
}
.good-page__money{
  display: flex;
  justify-content: space-between;
}
.good-page__price{
  padding: 2rem 0;
}

@media (max-width: 987px) {
  .good-page__title {
    padding: 20px 0;
    h2{
      text-align: center;
      font-size: 1.5rem;
      line-height: 2rem;

    }
  }
  .good-page__content {
    display: grid;
    // flex-direction: column;
    justify-items: center;
    align-items: center;
    grid-template-rows: 1fr;
    img{
      width: 100%;
    }
  }
  .good-page__info{
    max-height: none;
    img{
      // width: 0px;
      width: 100px;
      height: 120px;
    }
  }
  .good-page__about {
    padding: 0;
    p{
      font-size: 1rem;
      padding-top: 1rem;
      letter-spacing:normal;
      text-align: justify;
      &:first-letter {
        text-transform: uppercase;
    }
    }
  }
  .good-page__statistic{
    display: flex;
    justify-content: space-between;
    padding: 1rem 0;
    span{
      font-weight: 500;
    }
  }
  .good-page__price{
    padding: 1rem 0;
    h2{
      padding: 1rem 0;
      font-size: 1rem;
    }
  }
}