

.register{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items:center;
    margin: 50px 0;
    gap: 40px;
    a{ 
        padding-left: 10px;
        color: red;
        transition: .3s;
        &:hover{
            text-decoration: underline;
        }
    }
}

.form {
    display: flex;
    flex-direction: column;
    input {
        margin: 10px 0;
        padding: 20px 40px;
        width: 400px;
        font-size: 24px;
        border-radius: 40px;
        border: 1px solid lightgrey;
    }
    button {
        margin-top: 20px;
        transition: .3s;
    }
    button:disabled {
        opacity: .7;
        cursor: inherit;
    }
}